import { Component, OnInit, OnDestroy, Input, Output, EventEmitter, ChangeDetectorRef, OnChanges, SimpleChanges } from '@angular/core';
import { OrderService } from 'src/app/business/order.service';
import { Subscription } from 'rxjs';
import { IOrderItem, IOrderableItem, IAddOn } from 'src/app/business/abstract/cash-register.types';
import { NullOrderableItem } from 'src/app/business/abstract/null-objects';

@Component({
  selector: 'creg-menu-order-list',
  templateUrl: './order-list.component.html',
  styleUrls: ['./order-list.component.scss']
})
export class OrderListComponent implements OnInit, OnChanges, OnDestroy {

  @Input() item: IOrderableItem;
  @Input() menuItemName = '';
  @Output() increase = new EventEmitter();
  @Output() decrease = new EventEmitter();

  private orderSubscription: Subscription | undefined;

  public selectedAddOns: IAddOn[] = [];
  public addOnFormOpened: boolean;
  public orderItems: IOrderItem[];
  public editOrderHash: string;
  public basketLabel: string;
  public basketPrice: number;
  public basketDisabled: boolean;
  public modalIsOpened: boolean;
  public addonIsOpened: boolean;

  constructor(
    private orderService: OrderService,
    private cdRef: ChangeDetectorRef
  ) {
    this.orderItems = [];
    this.addOnFormOpened = false;
    this.editOrderHash = '';
    this.item = NullOrderableItem;
    this.orderSubscription = undefined;
    this.basketLabel = '';
    this.basketPrice = 0;
    this.basketDisabled = false;
    this.modalIsOpened = false;
    this.addonIsOpened = false;
  }

  ngOnInit(): void {
    this.orderSubscription = this.orderService.order.subscribe(order => {
      this.orderItems = order.items.filter(item => item.orderableId === this.item.id);
    });
  }

  ngOnChanges(changes: SimpleChanges): void {
    if ('item' in changes) {
      this.updateModalIsOpened();
    }
  }

  ngOnDestroy(): void {
    this.orderSubscription?.unsubscribe();
    this.orderItems = [];
  }

  public editAddOnsHandler(orderItem: IOrderItem): void {
    this.selectedAddOns = orderItem.addOns;
    this.editOrderHash = orderItem.addOnsHash;
    this.basketLabel = 'تغییر سفارش';
    this.basketPrice = orderItem.totalPrice;
    this.basketDisabled = true;
    this.openAddOnForm();
  }

  public orderHandler() {
    if (this.item !== null) {
      if (this.item.addOnList.length > 0) {
        this.basketLabel = 'افزودن به سفارش';
        this.basketPrice = this.item.price;
        this.basketDisabled = false;
        this.selectedAddOns = [];
        this.editOrderHash = '';
        this.openAddOnForm();
      }
      else {
        this.orderService.addItem(this.item, [], 1);
      }
    }
  }

  public basketClickHandler(): void {
    if (this.editOrderHash === '') {
      this.orderService.addItem(this.item, this.selectedAddOns, 1);
    }
    else {
      this.orderService.editItem(this.item, this.selectedAddOns, this.editOrderHash);
    }
    this.closeAddOnForm();
  }

  public closeAddOns() {
    this.closeAddOnForm();
  }

  public addOnsChanged(addOns: IAddOn[]): void {
    this.selectedAddOns = addOns;
    this.basketDisabled = this.editOrderHash === this.createOrderHash(this.item.id, addOns);
    let price = this.item.price;
    this.selectedAddOns.forEach(item => {price += item.price});
    this.basketPrice = price;
    this.cdRef.detectChanges();
  }

  public updateHandler(changed: number, addOnsHash: string, count: number): void {
    this.orderService.updateItem(addOnsHash, count + changed);
  }

  private closeAddOnForm(): void {
    this.addOnFormOpened = false;
    this.updateModalIsOpened();
  }

  private openAddOnForm(): void {
    this.addOnFormOpened = true;
    this.updateModalIsOpened();
  }

  private updateModalIsOpened(): void {
    this.modalIsOpened = this.addOnFormOpened && this.item !== null;
    if (this.modalIsOpened) {
      setTimeout(() => {
        this.addonIsOpened = true;
      }, 200);
    }
    else {
      this.addonIsOpened = false;
    }
  }

  private createOrderHash(itemId: number, addOns: IAddOn[]): string {
    let ids = [itemId];
    addOns.map(item => ids.push(item.id));
    return ids.sort().join('_');
  }
}
