import { Injectable } from '@angular/core';
import { BehaviorSubject, Observable } from 'rxjs';
import { HttpClient } from '@angular/common/http';
import { IMerchant } from './abstract/cash-register.types';
import { IServerMerchant, IServerResponse } from './abstract/api.types';
import { merchantConvertor } from './convertors/merchant.convertors';
import { NullMerchant } from './abstract/null-objects';
import { ApiService } from './api.service';

@Injectable({
  providedIn: 'root'
})
export class MerchantService {

  private __categoryId: number;
  private _categoryId: BehaviorSubject<number>;
  public categoryId: Observable<number>;

  private __parentCategoryIndex: number;
  private _parentCategoryIndex: BehaviorSubject<number>;
  public parentCategoryIndex: Observable<number>;

  private __merchant: IMerchant;
  private _merchant: BehaviorSubject<IMerchant>;
  public merchant: Observable<IMerchant>;

  private _menuScrollTop: BehaviorSubject<number>;
  public menuScrollTop: Observable<number>;

  private categoryOffsets: {offsetTop: number, categoryId: number}[];

  constructor(
    private httpClient: HttpClient,
    private apiService: ApiService
  ) {
    this.__categoryId = 0;
    this._categoryId = new BehaviorSubject<number>(this.__categoryId);
    this.categoryId = this._categoryId.asObservable();

    this.__merchant = NullMerchant;
    this._merchant = new BehaviorSubject<IMerchant>(this.__merchant);
    this.merchant = this._merchant.asObservable();

    this.__parentCategoryIndex = -1;
    this._parentCategoryIndex = new BehaviorSubject<number>(this.__parentCategoryIndex);
    this.parentCategoryIndex = this._parentCategoryIndex.asObservable();

    this._menuScrollTop = new BehaviorSubject<number>(0);
    this.menuScrollTop = this._menuScrollTop.asObservable();

    this.categoryOffsets = [];

    this.loadMerchantInfo();
  }

  public getMerchantId(): number {
    return this.__merchant.id;
  }

  public updateMenuScrollTop(scrollTop: number): void {
    this._menuScrollTop.next(scrollTop);
  }

  public changeCategoryId(id: number): void {
    this.__categoryId = id;
    this._categoryId.next(this.__categoryId);
  }

  public setCategoryBox(offsetTop: number, categoryId: number): void {
    this.categoryOffsets.push({offsetTop, categoryId});
  };
  public getCategoryBoxes(): {offsetTop: number, categoryId: number}[] {
    return this.categoryOffsets;
  }

  public selectParentCategory(id: number): void {
    this.__parentCategoryIndex = -1;
    if (this.__merchant) {
      this.__merchant.categories.forEach((item, index) => {
        if (item.id === id) {
          this.__parentCategoryIndex = index;
        }
      });
    }
    this._parentCategoryIndex.next(this.__parentCategoryIndex);
  }

  private loadMerchantInfo(): void {
    console.log(this.apiService.getApi());
    this.httpClient.get<IServerResponse<IServerMerchant>>(this.apiService.getApi() + 'Branch/GetMenu')
    .subscribe(
      res => {
        console.log(res.value);
        this.__merchant = merchantConvertor(res.value);
        console.log(this.__merchant);
        this._merchant.next(this.__merchant);
        this.changeCategoryId(this.__merchant?.categories[0]?.categories[0].id);
      },
      error => {
        //TODO
      }
    )
  }
}
