import { Component, OnInit, OnChanges, Input, HostBinding, SimpleChanges } from '@angular/core';

@Component({
  selector: 'creg-shared-checkbox',
  templateUrl: './checkbox.component.html',
  styleUrls: ['./checkbox.component.scss']
})
export class CheckboxComponent implements OnInit, OnChanges {

  @Input() selected: boolean = false;
  @Input() deactive: boolean = false;
  @Input() theme: string = 'yellow';
  @HostBinding('class.selected') selectedClass: boolean = false;
  @HostBinding('class.deactive') deactiveClass: boolean = false;
  @HostBinding('class.yellow') yellowClass: boolean = false;

  constructor() {}

  ngOnInit(): void {}

  ngOnChanges(changes: SimpleChanges): void {
    if ('selected' in changes) {
      this.selectedClass = changes.selected.currentValue;
    }
    if ('deactive' in changes) {
      this.deactiveClass = changes.deactive.currentValue;
    }
    if ('theme' in changes) {
      this.yellowClass = changes.theme.currentValue === 'yellow';
    }
  }
}
