import { IServerProfileOrder, IServerProfileOrderDetails } from "../abstract/api.types";
import { IProfileOrder, IProfileOrderDetials } from "../abstract/cash-register.types";
import * as moment from "jalali-moment";

export function profileOrderConvertor(serverProfileOrder: IServerProfileOrder): IProfileOrder {
    const orderDateTime = serverProfileOrder.createDate.split(' ');
    
    return {
        id: serverProfileOrder.orderId,
        name: serverProfileOrder.branchName,
        state: serverProfileOrder.status,
        stateCode: serverProfileOrder.statusCode,
        price: serverProfileOrder.paymentPrice,
        date: moment(orderDateTime[0], 'YYYY-MM-DD').locale('fa')
            .format('dddd، jD MMMM jYYYY'),
        time: orderDateTime[1],
        image: serverProfileOrder.branchLogoUrl,
        isNull: () => false
    }
}

export function orderDetailsConvertor(orderDetails: IServerProfileOrderDetails): IProfileOrderDetials {
    return orderDetails;
}