import { ISelectBehavior } from './ISelectBehavior';

export class SingleSelectBehavior<T> implements ISelectBehavior {
    private items: T[];
    private __selected: boolean[];

    constructor(items: T[]) {
        this.items = items;
        this.__selected = this.items.map(item => false);
    }

    public selectByIndex(index: number): boolean[] {
        this.__selected = this.__selected.map((item, i) => index === i ? !item : false)
        return this.__selected;
    }
}