import { Component, EventEmitter, Input, OnChanges, OnInit, Output, SimpleChanges } from '@angular/core';

@Component({
  selector: 'creg-payment-table-number',
  templateUrl: './table-number.component.html',
  styleUrls: ['./table-number.component.scss']
})
export class TableNumberComponent implements OnInit, OnChanges {

  @Input() reservedTable: number = 0;
  @Output() tableNumber = new EventEmitter<number>();

  public defaultCustomValue: number = 0;

  constructor() { }

  ngOnInit(): void {
  }

  ngOnChanges(changes: SimpleChanges): void {
      if ('reservedTable' in changes) {
        this.defaultCustomValue = changes.reservedTable.currentValue;
      }
  }

  public amountChanged($event: string | number): void {
    this.tableNumber.emit(+$event);
  }
}
