import { Component, EventEmitter, Output } from '@angular/core';
import { Router } from '@angular/router';
import { AuthConfig, AuthService } from 'src/app/business/auth.service';
import { OrderService } from 'src/app/business/order.service';

@Component({
  selector: 'creg-login-popup',
  templateUrl: './login-popup.component.html',
  styleUrls: ['./login-popup.component.scss']
})
export class LoginPopupComponent {

  @Output() close = new EventEmitter();

  public disableGuestLogin = false;
  private guestMobileNumber = '';
  private guestLoginClicked = false;

  constructor(
    private authService: AuthService,
    private orderService: OrderService,
    private router: Router
  ) {}

  guestLoginHandler(): void {
    if (this.guestLoginClicked || this.disableGuestLogin) return;
    this.guestLoginClicked = true;
    this.orderService.saveOrderWithMobile(this.guestMobileNumber).then(res => {
      this.guestLoginClicked = false;
      this.authService.closeLoginPopup();
      if (res) {
        this.router.navigate(['payment']);
      }
    });
  }

  memberLoginHandler(): void {
    this.authService.setRedirect('order');
    this.authService.openIFrame();
    this.authService.closeLoginPopup();
  }

  closeHandler(): void {
    if (!this.disableGuestLogin) {
      this.close.emit();
    }
  }

  mobileNumberHandler(newNumber: string): void {
    this.guestMobileNumber = newNumber;
    this.disableGuestLogin = newNumber.length > 0 && !/^09[0-9]{9}$/.test(newNumber);
  }

  contentClick($event: any): void {
    $event.stopPropagation();
    $event.preventDefault();
  }
}
