import { Injectable } from '@angular/core';
import { environment } from 'src/environments/environment';

@Injectable({
  providedIn: 'root'
})
export class ApiService {

  private api: string = '';

  constructor() {
    this.api = environment.apiUrl !== undefined && environment.apiUrl !== '' ? environment.apiUrl : this.getOriginAPI();
  }

  private getOriginAPI(): string {
    const port = environment.port !== undefined && environment.port !== '' ? ':' + environment.port : '';
    const protocol = environment.protocol !== undefined ? environment.protocol + ':' : window.location.protocol;
    const segments = window.location.pathname.split('/').slice(1,3).join('/');
    const pathname = segments === '' ? '' : '/' + segments;
    return protocol + '//' + window.location.hostname + port + pathname + '/api/';
  }

  public getApi(): string {
    return this.api;
  }
}
