import { Component, OnInit } from '@angular/core';
import { OrderService } from 'src/app/business/order.service';
import { IOrder } from 'src/app/business/abstract/cash-register.types';
import { NullOrder } from 'src/app/business/abstract/null-objects';
import { Location } from '@angular/common';
import { Router } from '@angular/router';
import { AuthConfig, AuthService } from 'src/app/business/auth.service';

@Component({
  selector: 'creg-order-layout',
  templateUrl: './order-layout.component.html',
  styleUrls: ['./order-layout.component.scss']
})
export class OrderLayoutComponent implements OnInit {

  public order: IOrder = NullOrder;
  public showDeleteAllModal = false;
  private basketClicked = false;

  constructor(
    private orderService: OrderService,
    private authService: AuthService,
    private location: Location,
    private router: Router
  ) {
    this.orderService.order.subscribe(res => {
      this.order = res;
    });
  }

  ngOnInit(): void {
  }

  public basketClickHandler(): void {
    if (this.basketClicked) return;
    this.basketClicked = true;
    this.authService.getAccessToken().then(accessToken => {
      this.basketClicked = false;
      if (accessToken === '') {
        this.authService.openLoginPopup();
      } else {
        this.orderService.saveOrder().then(result => {
          if (result) {
            this.router.navigate(['payment']);
          }
        });
      }
    });
  }

  public backHandler(): void {
    this.authService.setRedirect('twobackpage');
    this.location.back();
  }

  public deleteAllHandler(): void {
    this.showDeleteAllModal = true;
  }

  public onDescriptionChanged(description: string): void {
    this.orderService.updateDescription(description);
  }

  /**
   * approveDeletingAllHandler
   */
  public approveDeletingAllHandler() {
    this.orderService.deleteAll();
    this.location.back();
    this.closeDeleteAllModal();
  }

  /**
   * dismissDeleteAllHandler
   *///
  public dismissDeleteAllHandler() {
    this.closeDeleteAllModal();
  }

  /**
   * increaseHandler
   */
  public increaseHandler(addOnsHash: string, count: number) {
    this.orderService.updateItem(addOnsHash, count + 1);
  }

  /**
   * decreaseHandler
   */
  public decreaseHandler(addOnsHash: string, count: number) {
    this.orderService.updateItem(addOnsHash, count - 1);
  }

  private closeDeleteAllModal(): void {
    this.showDeleteAllModal = false;
  }
}
