import { Component, EventEmitter, HostListener, OnInit, Inject, HostBinding, Output, AfterViewInit, ElementRef, ChangeDetectorRef, Input } from '@angular/core';
import { DOCUMENT } from '@angular/common';

@Component({
  selector: 'creg-shared-bottom-sheet',
  templateUrl: './bottom-sheet.component.html',
  styleUrls: ['./bottom-sheet.component.scss']
})
export class BottomSheetComponent implements OnInit, AfterViewInit {

  @Output() close = new EventEmitter();
  @Input() fullViewEnabled = false;

  @HostListener('click', ['$event']) myClick(e: any) {
    e.stopPropagation();
    e.preventDefault();
  }
  @HostListener('window:popstate', ['$event'])
  opPopState(e: any) {
    e.stopPropagation();
    alert('pop state');
    e.preventDefault();
  }

  @HostBinding('class.full') full = false;

  private touchStartY = 0;
  private touchEndY = 0;
  public hasOverflow = true;

  constructor(
    @Inject(DOCUMENT) private document: Document,
    private elementRef: ElementRef,
    private cdRef: ChangeDetectorRef
  ) {
    document.addEventListener('touchstart', e => {
      this.touchStartY = e.changedTouches[0].screenY;
    });
    document.addEventListener('touchend', e => {
      this.touchEndY = e.changedTouches[0].screenY;
      if (this.touchEndY + 30 < this.touchStartY) {
        this.onSwipeUp()
      }
      if (this.touchEndY > 30 + this.touchStartY) {
        this.onSwipeDown()
      }
    });
  }

  ngOnInit(): void {
  }

  ngAfterViewInit(): void {
    const container = this.elementRef.nativeElement.querySelector('.container');
    this.hasOverflow = container.offsetHeight < container.scrollHeight;
    this.cdRef.detectChanges();
  }

  private onSwipeDown() {
    if (!this.fullViewEnabled) return;
    if (this.full === false) {
      this.close.emit();
    }
    else {
      this.full = false;
    }
  }
  private onSwipeUp() {
    if (!this.fullViewEnabled) return;
    this.full = true;
  }
}
