import { HttpEvent, HttpHandler, HttpInterceptor, HttpResponse } from '@angular/common/http';
import { Injectable, Injector } from '@angular/core';
import { Observable, of } from 'rxjs';
import { IServerProfileOrder, IServerProfileOrderDetails, IServerResponse } from 'src/app/business/abstract/api.types';

import { merchantResponse } from './data/merchant';

@Injectable()
export class FakeBackendInterceptor implements HttpInterceptor {
    constructor(private injector: Injector) {}
    intercept(request: any, next: HttpHandler): Observable<HttpEvent<any>> {
        const localAPI = 'http://localhost:8080/api/';
        if (request.method === 'GET' && request.url.indexOf(localAPI + "Branch/GetMenu") === 0) {
            return this.getBody(merchantResponse);
        }
        if (request.method === 'GET' && request.url.indexOf('UserOrderDetails') > -1) {
            return this.getOrderDetails();
        }
        if (request.method === 'GET' && request.url.indexOf('UserOrders?msisdn') > -1) {
            return this.getProfileOrders();
        }
        return (next.handle(request))
    }

    getOrderDetails(): Observable<HttpResponse<any>> {
        const body: IServerProfileOrderDetails = {
            id: 2,
            orderNumber: 26570,
            orderState: 'یک عدد برگشت خورده',
            tableNumber: '6',
            totalCount: 5,
            orderAmount: 1750000,
            taxPercentRate: 9,
            taxAmount: 157500,
            tip: 200000,
            serviceCost: 0,
            discount: 100000,
            totalAmount: 2007500,
            prePayment: 1000000,
            payableAmount: 1007500,
            items: [
                {
                    title: 'دبل اسپرسو لیوان بزرگ',
                    description: 'اسپرسو، شیر، شکلات فندقی، خامه',
                    count: 1,
                    state: '۱ عدد برگشت خورده',
                    price: 500000
                },
                {
                    title: 'دبل اسپرسو لیوان بزرگ',
                    description: 'اسپرسو، شیر',
                    count: 3,
                    state: '',
                    price: 750000
                }
            ]
        }
        return of(new HttpResponse({status: 200, body: this.getResponse(body)}));
    }

    getProfileOrders(): Observable<HttpResponse<any>> {
        const body: IServerProfileOrder[] = [
            {
                orderId: 1,
                branchName: 'رستوران لئون',
                status: 'کنسل شده',
                paymentPrice: 200000,
                createDate: '2022-12-28 01:36',
                statusCode: 2,
                branchLogoUrl: 'MerchantsImages/1/logo.png'
            }
        ];
        return of(new HttpResponse({status: 200, body: this.getResponse(body)}));
    }

    getBody(body: any): Observable<HttpResponse<any>> {
        return of(new HttpResponse({ status: 200, body: body }));
    }

    getResponse<T>(value: T): IServerResponse<T> {
        return {
            value: value,
            valueOrDefault: value,
            errors: [],
            isFailed: false,
            isSuccess: true,
            reasons: [],
            successes: []
        }
    }
}