import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { BehaviorSubject, Observable } from 'rxjs';
import { ApiService } from './api.service';

@Injectable({
  providedIn: 'root'
})
export class WalletService {

  private __walletData: number;
  private _wallet: BehaviorSubject<number>;
  public wallet: Observable<number>;

  constructor(
    private httpClient: HttpClient,
    private apiService: ApiService
  ) {
    this.__walletData = 0;
    this._wallet = new BehaviorSubject<number>(this.__walletData);
    this.wallet = this._wallet.asObservable();

    this.httpClient.get<number>(this.apiService.getApi() + 'wallet').subscribe(res => {
      this.__walletData = res;
      this._wallet.next(this.__walletData);
    });
  }
}
