import { Component } from '@angular/core';
import { Router } from '@angular/router';
import { Observable } from 'rxjs';
import { IMerchant } from 'src/app/business/abstract/cash-register.types';
import { ApiService } from 'src/app/business/api.service';
import { AuthService } from 'src/app/business/auth.service';
import { MerchantService } from 'src/app/business/merchant.service';

@Component({
  selector: 'creg-menu-header',
  templateUrl: './header.component.html',
  styleUrls: ['./header.component.scss']
})
export class MenuHeaderComponent {

  public merchant$: Observable<IMerchant | undefined>;

  constructor(
    private merchantService: MerchantService,
    private authService: AuthService,
    private apiService: ApiService,
    private router: Router
  ) {
    this.merchant$ = this.merchantService.merchant;
  }
  
  goToProfile(): void {
    this.authService.checkAuth().then(accessToken => {
      console.log({accessToken});
      if (accessToken !== '') {
        this.router.navigate(['profile']);
      }
      else {
        this.authService.setRedirect('profile');
        this.authService.openIFrame();
      }
    });
  }

}
