import { Component, EventEmitter, OnInit, Output } from '@angular/core';
import { AuthService } from 'src/app/business/auth.service';
import { WalletService } from 'src/app/business/wallet.service';

@Component({
  selector: 'creg-payment-wallet',
  templateUrl: './wallet.component.html',
  styleUrls: ['./wallet.component.scss']
})
export class WalletComponent implements OnInit {

  @Output() wallet = new EventEmitter<number>();

  public deactive = true;
  public selected = false;
  public amount = 0;
  public isLoggedIn = false;

  constructor(
    private walletService: WalletService,
    private authService: AuthService
  ) {
    this.walletService.wallet.subscribe(res => {
      this.updateAmount(res);
    }, err => {
      this.updateAmount(0);
    });
    this.isLoggedIn = this.authService.isLoggedIn();
  }

  ngOnInit(): void {
    console.log('wallet ng on init', this.isLoggedIn)
  }

  public loginHandler($event: any): void {
    $event.preventDefault();
    this.authService.setRedirect('payment');
    this.authService.openIFrame();
    $event.stopPropagation();
  }

  public clickHandler(): void {
    this.selected = !this.selected;
    this.notify();
  }

  public updateAmount(amount: number): void {
    this.amount = amount;
    this.deactive = this.amount === 0;
    this.notify();
  }

  public notify(): void {
    this.wallet.next(this.selected ? this.amount : 0);
  }

}
