import { IServerAddOn, IServerAddOnGroup, IServerProduct, IServerMenuItem, IServerChildrenCategory, IServerCategory, IServerMerchant } from '../abstract/api.types';
import { IAddOn, IAddOnGroup, IProduct, IMenuItem, IOrderableItem, ICategory, IParentCategory, IMerchant } from '../abstract/cash-register.types';
import { NullOrderableItem } from '../abstract/null-objects';

export function merchantConvertor(serverMerchant: IServerMerchant): IMerchant {
  return {
    id: serverMerchant.id,
    rate_number: serverMerchant.rateNumber,
    rate_times: serverMerchant.rateTimes,
    name: serverMerchant.fullName,
    icon: serverMerchant.logoUrl,
    categories: serverMerchant.categories.map(
      serverParentCategory => parentCategoryConvertor(
        serverParentCategory,
        serverMerchant.menuItems
      )
    )
  };
}

function parentCategoryConvertor(serverCategory: IServerCategory, serverMenuItems: IServerMenuItem[]): IParentCategory {
  return {
    id: serverCategory.id,
    icon: serverCategory.iconUrl,
    title: serverCategory.persianName,
    title_en: serverCategory.englishName,
    categories: serverCategory.childrenCategories === null ? [] :
      serverCategory.childrenCategories?.map(
        item => categoryConvertor(item, serverMenuItems)
      )
  };
}

function categoryConvertor(serverCategory: IServerChildrenCategory, serverMenuItems: IServerMenuItem[]): ICategory {
    return {
      id: serverCategory.id,
      title: serverCategory.name,
      icon: serverCategory.iconUrl,
      menuItems: serverMenuItems
        .filter(serverMenuItem => serverMenuItem.categoryId === serverCategory.id)
        .map(serverMenuItem => menuItemConvertor(serverMenuItem))
    };
}

function menuItemConvertor(serverMenuItem: IServerMenuItem): IMenuItem {
    const result: IMenuItem = {
      id: serverMenuItem.id,
      name: serverMenuItem.name,
      rate: serverMenuItem.rate,
      description: serverMenuItem.description,
      basePrice: serverMenuItem.price,
      products: [],
      addOnGroups: serverMenuItem.addOnGroups !== undefined ? serverMenuItem.addOnGroups.map(
        serverAddOnGroup => addOnGroupConvertor(serverAddOnGroup)
      ): [],
      image: serverMenuItem.imageUrl,
      isActive: serverMenuItem.isAvailable,
      orderableItem: NullOrderableItem
    };
    result.products = serverMenuItem.products !== undefined ? 
      serverMenuItem.products.map(
        item => productConvertor(item, result)
      )
      : [];
    result.orderableItem = menuItemToOrderableItem(result);
    return result;
}

function addOnGroupConvertor(serverAddonGroup: IServerAddOnGroup): IAddOnGroup {
    return {
      id: serverAddonGroup.id,
      name: serverAddonGroup.name,
      selectedAddOnLimit: serverAddonGroup.selectedAddOnLimit,
      addOns: serverAddonGroup.addOns.map(serverAddOn => addOnConvertor(serverAddOn))
    };
}

function menuItemToOrderableItem(serverMenuItem: IMenuItem): IOrderableItem {
    if (serverMenuItem.products === null || serverMenuItem.products.length > 0) return NullOrderableItem;
    return {
      id: serverMenuItem.id,
      title: serverMenuItem.name,
      price: serverMenuItem.basePrice,
      addOnList: serverMenuItem.addOnGroups,
      isActive: serverMenuItem.isActive,
      isNull: () => false,
      imageSrc: serverMenuItem.image,
      hasAddOns: () => serverMenuItem.addOnGroups.length > 0
    }
}

function productConvertor(serverProduct: IServerProduct, menuItem: IMenuItem): IProduct {
    const product: IProduct = {
      id: serverProduct.id,
      name:  serverProduct.name,
      description: serverProduct.description,
      price: serverProduct.price,
      displayOrder: serverProduct.displayOrder,
      isActive: serverProduct.isAvailable,
      orderableItem: NullOrderableItem,
      addOnGroups: serverProduct.addOnGroups !== undefined ? serverProduct.addOnGroups.map(
        serverAddOnGroup => addOnGroupConvertor(serverAddOnGroup)
      ): [],
    };
    product.orderableItem = productToOrderableItem(product, menuItem);
    return product;
}

function addOnConvertor(serverAddOn: IServerAddOn): IAddOn {
    return {
      id: serverAddOn.id,
      name: serverAddOn.name,
      code: '' + serverAddOn.code,
      price: serverAddOn.price,
      addOnGroupId: serverAddOn.addOnGroupId
    };
}

function productToOrderableItem(product: IProduct, menuItem: IMenuItem): IOrderableItem {
    return {
      id: product.id,
      title: menuItem.name + ' | ' + product.name,
      addOnList: product.addOnGroups,
      price: product.price,
      isActive: product.isActive,
      isNull: () => false,
      imageSrc: menuItem.image,
      hasAddOns: () => product.addOnGroups.length > 0
    }
}