import { Injectable } from '@angular/core';
import { Router } from '@angular/router';
import { AuthOptions, LogoutAuthOptions, OidcSecurityService, OpenIdConfiguration, PopupOptions } from 'angular-auth-oidc-client';
import { BehaviorSubject } from 'rxjs';
import { ProfileService } from './profile.service';

@Injectable({
  providedIn: 'root'
})
export class AuthService {

  private parsaToken = '';
  private commonToken = '';
  private loggedIn = false;

  private _loginModalDisplay = new BehaviorSubject<boolean>(false);
  public loginModalDisplay = this._loginModalDisplay.asObservable();

  private _IFrameDisplay = new BehaviorSubject<boolean>(false);
  public IFrameDisplay = this._IFrameDisplay.asObservable();

  private configurations: OpenIdConfiguration[] = [];

  constructor(
    private oidcSecurityService: OidcSecurityService,
    private router: Router
  ) {}

  public isLoggedIn(): boolean {
    return this.loggedIn;
  }

  public getAccessToken(): Promise<string> {
    return new Promise((resolve, reject) => {
      this.oidcSecurityService.getAccessToken().subscribe(accessToken => {
        if (accessToken !== null) { resolve(accessToken); }
        else resolve('');
      })
    })
  }

  public checkAuth(): Promise<string> {
    return new Promise((resolve, reject) => {
      this.oidcSecurityService.checkAuthMultiple().subscribe(([{ isAuthenticated, userData, accessToken }]) => {
        this.loggedIn = accessToken !== '' && accessToken !== null;
        resolve(this.loggedIn ? accessToken : '');
      });
    })
  }

  public setRedirect(route: string): void {
    localStorage.setItem('redirect', route);
  }

  public getRedirect(): string | null {
    return localStorage.getItem('redirect');
  }

  public logout(): void {
    this.oidcSecurityService.logoff('parsa-client').subscribe(res => {
      this.loggedIn = false;
    });
  }

  private getConfigId(configIndex: number): string | undefined {
    console.log(this.oidcSecurityService.getConfigurations()[configIndex]);
    return this.oidcSecurityService.getConfigurations()[configIndex].configId;
  }

  public openIFrame(): void {
    this.oidcSecurityService.authorizeWithPopUp(undefined, undefined, this.getConfigId(0))
    .subscribe(
      ({ isAuthenticated, userData, accessToken, errorMessage }) => {
        if (isAuthenticated && accessToken !== null) {
          this.loggedIn = true;
        }
      },
      err => {
        console.log({err});
      }
    );
  }

  public closeIFrame(): void {
    this._IFrameDisplay.next(false);
  }

  public openLoginPopup(): void {
    this._loginModalDisplay.next(true);
  }

  public closeLoginPopup(): void {
    this._loginModalDisplay.next(false);
  }
}

export declare const enum AuthConfig {
  'profile' = 0,
  'orders' = 1
}