import { Component, EventEmitter, Input, OnInit, OnChanges, Output, SimpleChanges, HostBinding } from '@angular/core';
import { ConfirmBoxState } from './confirm-box.types';

@Component({
  selector: 'creg-shared-confirm-box',
  templateUrl: './confirm-box.component.html',
  styleUrls: ['./confirm-box.component.scss']
})
export class ConfirmBoxComponent implements OnInit, OnChanges {

  @Input() state: ConfirmBoxState = 'default';
  @Input() title: string = '';
  @Input() description: string = '';
  @Input() primaryActionText: string = '';
  @Input() primaryActionClassList: string = '';
  @Input() secondaryActionText: string = '';
  @Input() secondaryActionClassList: string = '';

  @HostBinding('class.success') success: boolean = false;
  @HostBinding('class.error') error: boolean = false;
  @HostBinding('class.warning') warning: boolean = false;

  @Output() primary = new EventEmitter();
  @Output() secondary = new EventEmitter();

  constructor() { }

  ngOnInit(): void {
  }

  ngOnChanges(changes: SimpleChanges): void {
    if ('state' in changes) {
      this.success = changes.state.currentValue === 'success';
      this.error = changes.state.currentValue === 'error';
      this.warning = changes.state.currentValue === 'warning';
    }
  }

  primaryHandler(e: any): void {
    this.primary.emit(e);
  }

  secondaryHandler(e: any): void {
    this.secondary.emit(e);
  }
}
