import { Component, OnInit, Input, AfterViewInit } from '@angular/core';
import { ICategory } from 'src/app/business/abstract/cash-register.types';
import { MerchantService } from 'src/app/business/merchant.service';
import { OrderService } from 'src/app/business/order.service';

@Component({
  selector: 'creg-menu-item-list',
  templateUrl: './item-list.component.html',
  styleUrls: ['./item-list.component.scss']
})
export class MenuItemListComponent implements OnInit, AfterViewInit {
  @Input() categories: ICategory[];
  private offsets: ICategoryOffset[];

  constructor(
    private merchantService: MerchantService,
    private orderService: OrderService
  ) {
    this.categories = [];
    this.offsets = [];

    this.merchantService.menuScrollTop.subscribe(res => {
      const tune = 100;
      this.offsets.forEach((item, index) => {
        if (
          res + tune >= item.top &&
          (
            (this.offsets.length === index + 1) ||
            this.offsets[index+1].top > res + tune
          )
        ) {
          this.merchantService.changeCategoryId(item.id);
        }
      })
    });

    this.orderService.order.subscribe(order => {
      //
    });
  }

  ngOnInit(): void {}

  ngAfterViewInit(): void {
    Array.from(document.getElementsByClassName('category')).forEach(item => {
      this.offsets.push({
        id: +(item.getAttribute('id') + '').substring(8),
        top: (item as HTMLElement).offsetTop
      });
    });
  }

}

interface ICategoryOffset {
  top: number;
  id: number;
}
