import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { IOrderItem } from 'src/app/business/abstract/cash-register.types';
import { NullOrderItem } from 'src/app/business/abstract/null-objects';
import { ConfigService } from 'src/app/business/config.service';

@Component({
  selector: 'creg-menu-order-item',
  templateUrl: './order-item.component.html',
  styleUrls: ['./order-item.component.scss']
})
export class OrderItemComponent implements OnInit {

  @Input() orderItem: IOrderItem = NullOrderItem;
  @Input() preTitle = '';
  @Output() changed = new EventEmitter<number>();
  @Output() edit = new EventEmitter();

  public maxOrderItemCount = 1000;

  constructor(
    private config: ConfigService
  ) {
    this.maxOrderItemCount = this.config.getMaxOrderItemCount();
  }

  ngOnInit(): void {
  }

  increaseCount(): void {
    this.changed.emit(1);
  }

  decreaseCount(): void {
    this.changed.emit(-1);
  }

  editAddOns(): void {
    this.edit.emit();
  }
}
