import { Component, OnDestroy, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { Observable, Subscription } from 'rxjs';
import { IMerchant } from 'src/app/business/abstract/cash-register.types';
import { NullMerchant } from 'src/app/business/abstract/null-objects';
import { MerchantService } from 'src/app/business/merchant.service';

@Component({
  selector: 'creg-homepage-layout',
  templateUrl: './homepage-layout.component.html',
  styleUrls: ['./homepage-layout.component.scss']
})
export class HomepageLayoutComponent implements OnInit, OnDestroy {

  public loadedImages = 0;
  public merchant: IMerchant;
  public showWelcome = false;
  private merchantSubscription: Subscription | null;

  constructor(
    private merchantService: MerchantService,
    private router: Router
  ) {
    this.merchant = NullMerchant;
    this.merchantSubscription = null;
  }

  ngOnInit(): void {
    this.merchantSubscription = this.merchantService.merchant.subscribe({
      next: res => {
        this.merchant = res;
        if (res.categories.length === 1) {
          setTimeout(() => {
            const category = res.categories[0];
            this.router.navigate(['menu', category.id, category.title]);
          }, 500);
        }
      }
    })
  }

  ngOnDestroy(): void {
    this.merchantSubscription?.unsubscribe();
  }

  logoLoaded($event: any): void {
    this.showWelcome = true;
  }
}
