import { Component, HostListener, OnDestroy, OnInit } from '@angular/core';
import { Location } from '@angular/common';
import { IProfileOrder } from 'src/app/business/abstract/cash-register.types';
import { ProfileService } from 'src/app/business/profile.service';
import { Subscription } from 'rxjs';
import { Router } from '@angular/router';

@Component({
  selector: 'creg-profile-orders-layout',
  templateUrl: './profile-orders-layout.component.html',
  styleUrls: ['./profile-orders-layout.component.scss']
})
export class ProfileOrdersLayoutComponent implements OnInit, OnDestroy {

  public orders: IProfileOrder[] = [];
  private profileSubscription: Subscription;
  private ordersSubscription: Subscription;

  @HostListener('scroll', ['$event']) // for window scroll events
  onScroll(event: any) {
    if (event.target.scrollHeight
      - event.target.scrollTop
      - event.target.offsetHeight < 100) {
        this.profileService.loadProfileOrders();
    }
  }

  constructor(
    private profileService: ProfileService,
    private location: Location,
    private router: Router
  ) {
    this.profileSubscription = this.profileService.profile.subscribe(res => {
      if (res.phoneNumber !== '') {
        this.profileService.loadProfileOrders();
      }
    });

    this.ordersSubscription = this.profileService.ordersState.subscribe(res => {
      this.orders = res.orders;
    });
  }

  ngOnInit(): void {
  }

  ngOnDestroy(): void {
    this.profileSubscription?.unsubscribe();
    this.ordersSubscription?.unsubscribe();
  }

  backHandler(): void {
    this.location.back();
  }

  orderClickHandler(index: number): void {
    this.profileService.setOrderReceiptIndex(index);
    this.router.navigate(['receipt']);
  }
}
