import { Component, Input, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { IProfileOrder } from 'src/app/business/abstract/cash-register.types';
import { NullProfileOrder } from 'src/app/business/abstract/null-objects';
import { ProfileService } from 'src/app/business/profile.service';

@Component({
  selector: 'creg-profile-order-item',
  templateUrl: './order-item.component.html',
  styleUrls: ['./order-item.component.scss']
})
export class OrderItemComponent implements OnInit {

  @Input() order = NullProfileOrder;
  @Input() orderIndex = -1;

  constructor(
    private profileService: ProfileService,
    private router: Router
  ) { }

  ngOnInit(): void {
  }

  receiptHandler(): void {
    this.profileService.setOrderReceiptIndex(this.orderIndex);
    this.router.navigate(['receipt']);
  }

}
