import { Component, EventEmitter, HostListener, Input, OnInit, OnChanges, Output, SimpleChanges, ViewChild } from '@angular/core';

@Component({
  selector: 'creg-order-description',
  templateUrl: './description.component.html',
  styleUrls: ['./description.component.scss']
})
export class DescriptionComponent implements OnInit, OnChanges {

  public showModal = false;
  public value: string = '';

  @ViewChild('textarea') textarea: any;
  @Output() changedValue = new EventEmitter<string>();
  @Input() description: string = '';
  @HostListener('click') onClick() {
    this.showModal = !this.showModal;
    if (this.showModal) {
      setTimeout(() => {
        this.textarea.nativeElement.focus();
      }, 10)
    }
  }

  constructor() { }

  ngOnInit(): void {
  }

  ngOnChanges(changes: SimpleChanges): void {
    if ('description' in changes) {
      this.value = changes.description.currentValue;
    }
  }

  closeModalHandler($event: any): void {
    $event.stopPropagation();
    this.showModal = false; 
    $event.preventDefault();
  }

  containerClickHandler($event: any): void {
    $event.stopPropagation();
    $event.preventDefault();
  }

  valueChanged($event: any): void {
    if ($event.length > 500) {
      this.textarea.nativeElement.value = $event.substring(0, 500);
    }
  }

  saveChanges($event: any): void {
    $event.stopPropagation();
    this.changedValue.emit(this.value);
    this.showModal = false;
    $event.preventDefault();
  }

  deleteAllHandler($event: any): void {
    $event.stopPropagation();
    this.value = '';
    this.changedValue.emit(this.value);
    this.showModal = false;
    $event.preventDefault();
  }
}
