import { ISelectBehavior } from "./ISelectBehavior";

export class MultiSelectBehavior<T> implements ISelectBehavior {
    private limit: number;
    private selectedCount: number;
    private items: T[];
    private __selected: boolean[];

    constructor(items: T[], limit: number) {
        this.limit = limit;
        this.items = items;
        this.selectedCount = 0;
        this.__selected = this.items.map(item => false);
    }

    selectByIndex(index: number): boolean[] {
        if (this.selectedCount !== this.limit || this.__selected[index]) {
            this.__selected[index] = !this.__selected[index];
            this.selectedCount += this.__selected[index] ? 1 : -1;
        }
        return this.__selected;
    }
}