import { Component, EventEmitter, OnInit, Output } from '@angular/core';
import { AuthService } from 'src/app/business/auth.service';

@Component({
  selector: 'creg-payment-reward',
  templateUrl: './reward.component.html',
  styleUrls: ['./reward.component.scss']
})
export class RewardComponent implements OnInit {

  @Output() reward = new EventEmitter<number>();
  public amount: number = 0;
  public options = [100000, 200000, 500000];
  public defaultCustomValue: number = 0;
  public modalIsopened = false;

  constructor(
    private authService: AuthService
  ) { }

  ngOnInit(): void {
  }

  public showModal(): void {
    this.modalIsopened = true;
  }

  public selectOption(amount: number): void {
    this.defaultCustomValue = amount / 10;
    this.setAmount(amount);
  }

  public amountChanged($event: string | number): void {
    this.setAmount(+$event * 10);
  }

  public saveAmountAndClose(): void {
    this.reward.emit(this.amount);
    this.closeModal();
  }

  public closeModal(): void {
    this.modalIsopened = false;
  }

  private setAmount(amount: number): void {
    this.amount = amount;
    this.defaultCustomValue = this.amount / 10;
  }
}
