import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { IOrderItem } from 'src/app/business/abstract/cash-register.types';
import { NullOrderItem } from 'src/app/business/abstract/null-objects';
import { OrderService } from 'src/app/business/order.service';

@Component({
  selector: 'creg-shared-order-item',
  templateUrl: './order-item.component.html',
  styleUrls: ['./order-item.component.scss']
})
export class SharedOrderItemComponent implements OnInit {

  @Input() orderItem: IOrderItem = NullOrderItem;
  @Input() editable: boolean = false;
  @Output() increase = new EventEmitter();
  @Output() decrease = new EventEmitter();

  constructor(
    private orderService: OrderService
  ) { }

  ngOnInit(): void {
  }

  public increaseHandler(): void {
    if (this.editable) {
      this.increase.emit();
      // this.orderService.updateItem(this.orderItem.addOnsHash, this.orderItem.count + 1);
    }
  }

  public decreaseHandler(): void {
    if (this.editable) {
      this.decrease.emit();
      // this.orderService.updateItem(this.orderItem.addOnsHash, this.orderItem.count - 1);
    }
  }
}
