import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { BehaviorSubject, Observable } from 'rxjs';
import { IServerPagination, IServerProfile, IServerProfileOrder, IServerProfileOrderDetails, IServerProfileOrdersPagination, IServerResponse } from './abstract/api.types';
import { IProfile, IProfileOrderDetials, IProfileOrdersState } from './abstract/cash-register.types';
import { NullServerProfile } from './abstract/null-objects';
import { ApiService } from './api.service';
import { orderDetailsConvertor, profileOrderConvertor } from './convertors/profile.convertors';

@Injectable({
  providedIn: 'root'
})
export class ProfileService {

  private __profile: IProfile;
  private _profile: BehaviorSubject<IProfile>;
  public profile: Observable<IProfile>;

  private __ordersState: IProfileOrdersState;
  private _ordersState: BehaviorSubject<IProfileOrdersState>;
  public ordersState: Observable<IProfileOrdersState>;

  private orderReceiptIndex: number;

  private __orderDetails: IProfileOrderDetials;
  private _orderDetails: BehaviorSubject<IProfileOrderDetials>;
  public orderDetails: Observable<IProfileOrderDetials>;

  private loadingOrders = false;

  constructor(
    private httpClient: HttpClient,
    private apiService: ApiService
  ) {
    this.__profile = NullServerProfile;
    this._profile = new BehaviorSubject(this.__profile);
    this.profile = this._profile.asObservable();

    const initialState: IProfileOrdersState = {
      orders: [],
      loaded: false,
      lastPage: 0
    }

    this.__ordersState = initialState;
    this._ordersState = new BehaviorSubject(this.__ordersState);
    this.ordersState = this._ordersState.asObservable();

    this.__orderDetails = {} as IServerProfileOrderDetails;
    this._orderDetails = new BehaviorSubject(this.__orderDetails);
    this.orderDetails = this._orderDetails.asObservable();

    this.orderReceiptIndex = -1;
  }

  /**
   * loadProfile
   */
  public loadProfile() {
    const url = this.apiService.getApi() + 'User';
    this.httpClient.get<IServerResponse<IServerProfile>>(url).subscribe(res => {
      this.__profile = res.value;
      this._profile.next(this.__profile);
    })
  }

  /**
   * loadProfileOrders
   */
  public loadProfileOrders() {
    if (this.loadingOrders || this.__ordersState.loaded) return;
    this.loadingOrders = true;
    const pageSize = 10;
    const pageIndex = this.__ordersState.lastPage;
    const url = this.apiService.getApi() + 'Order/CustomerOrders?pageSize='+
      pageSize+'&pageIndex='+pageIndex;
    this.httpClient.get<IServerResponse<IServerProfileOrdersPagination<IServerProfileOrder>>>(url).subscribe(res => {
      this.__ordersState.lastPage++;
      res.value.paginatedCustomerOrderList.data.map(item => profileOrderConvertor(item)).forEach(item => {
        this.__ordersState.orders.push(item);
      });
      if (this.__ordersState.orders.length === res.value.paginatedCustomerOrderList.count) {
        this.__ordersState.loaded = true;
      }
      this._ordersState.next(this.__ordersState);
      this.loadingOrders = false;
    },
    err => {
      this.loadingOrders = false;
    });
  }

  /**
   * loadProfileOrderDetails
   */
  public loadOrderDetails(): void {
    if (this.__ordersState.orders[this.orderReceiptIndex] === undefined) {
      return;
    }
    else if (this.__ordersState.orders[this.orderReceiptIndex].details === undefined) {
      this.fetchOrderDetails(
        this.__ordersState.orders[this.orderReceiptIndex].id
      );
    }
    else {
      this.__orderDetails = this.__ordersState.orders[this.orderReceiptIndex].details as IProfileOrderDetials;
      this._orderDetails.next(this.__orderDetails);
    }
  }

  private fetchOrderDetails(orderId: number) {
    const url = this.apiService.getApi() + 'Order/CustomerOrderDetails?orderId=' + orderId;
    this.httpClient.get<IServerResponse<IServerProfileOrderDetails>>(url).subscribe(res => {
      const orderDetails = orderDetailsConvertor(res.value);
      this.__ordersState.orders[this.orderReceiptIndex].details = orderDetails;
      this.__orderDetails = orderDetails;
      this._orderDetails.next(this.__orderDetails);
    });
  }

  /**
   * setOrderReceiptIndex
   */
  public setOrderReceiptIndex(index: number): void {
    this.orderReceiptIndex = index;
  }
}
