import { Component, HostBinding, Input, OnInit, AfterViewInit, Output, EventEmitter, ChangeDetectorRef, OnDestroy } from '@angular/core';
import { IAddOn, IAddOnGroup } from 'src/app/business/abstract/cash-register.types';
import { SingleSelectBehavior } from './selectors/single-select';
import { ISelectBehavior } from './selectors/ISelectBehavior';
import { MultiSelectBehavior } from './selectors/multi-select';
import { NullAddOnGroup } from 'src/app/business/abstract/null-objects';

@Component({
  selector: 'creg-menu-addon-box',
  templateUrl: './addon-box.component.html',
  styleUrls: ['./addon-box.component.scss']
})
export class AddonBoxComponent implements OnInit, OnDestroy, AfterViewInit {

  @Input() addOnGroup: IAddOnGroup;
  @Input() defaultAddOns: IAddOn[] = [];
  @Output() change: EventEmitter<IAddOn[]>;
  public hide = false;
  public selectedAddOns: boolean[];

  private _selectBehavior: ISelectBehavior | null;

  constructor(
    private cdRef: ChangeDetectorRef
  ) {
    this.addOnGroup = NullAddOnGroup;
    this.selectedAddOns = [];
    this._selectBehavior = new SingleSelectBehavior<IAddOn>([]);;
    this.change = new EventEmitter<IAddOn[]>();
  }

  ngOnInit(): void {
    this.initSelectBehavior();
  }

  ngOnDestroy(): void {
      this.cdRef.detach();
  }

  ngAfterViewInit(): void {
    this.initSelectedAddOns();
    this.cdRef.detectChanges();
  }

  private initSelectBehavior(): void {
    if (this.addOnGroup !== undefined && this.addOnGroup !== null) {
      this.createSelectBehavior(this.addOnGroup.selectedAddOnLimit, this.addOnGroup.addOns);
    }
    else {
      setTimeout(() => this.initSelectBehavior(), 100);
    }
  }

  private initSelectedAddOns(): void {
    const ids = this.defaultAddOns.map(item => item.id);
    this.addOnGroup.addOns.forEach((item, index) => {
      if (ids.includes(item.id)) {
        this._selectAddonHandler(index);
      }
    })
  }

  private createSelectBehavior(limit: number, items: IAddOn[]): void {
    if (limit === 1) {
      this._selectBehavior = new SingleSelectBehavior<IAddOn>(items);
    }
    else {
      this._selectBehavior = new MultiSelectBehavior<IAddOn>(items, limit);
    }
  }
  
  public toggleHandler(): void {
    this.hide = !this.hide;
  }

  public selectAddonHandler(index: number): void {
    this.change.next(this._selectAddonHandler(index));
  }

  private _selectAddonHandler(index: number): IAddOn[] {
    this.selectedAddOns = this._selectBehavior?.selectByIndex(index) || [];
    const addOns: IAddOn[] = [];
    this.selectedAddOns.forEach((selected, index) => {
      if (selected) {
        addOns.push(this.addOnGroup.addOns[index]);
      }
    });
    return addOns;
  }
}
