import { Component, OnInit, Input, Output, EventEmitter, OnChanges, SimpleChanges } from '@angular/core';
import { IAddOn, IAddOnGroup, IOrderableItem } from 'src/app/business/abstract/cash-register.types';
import { NullOrderableItem } from 'src/app/business/abstract/null-objects';
import { OrderService } from 'src/app/business/order.service';

@Component({
  selector: 'creg-menu-addons-popup',
  templateUrl: './addons-popup.component.html',
  styleUrls: ['./addons-popup.component.scss']
})
export class AddonsPopupComponent implements OnInit, OnChanges {

  @Input() item: IOrderableItem;
  @Input() defaultAddOns: IAddOn[] = [];
  @Output() close = new EventEmitter();
  @Output() changed = new EventEmitter();

  public totalPrice: number = 0;
  public selectedAddOns: IAddOn[] = [];

  constructor() {
    this.item = NullOrderableItem;
  }

  ngOnInit(): void {
    this.totalPrice = this.item.price;
  }

  ngOnChanges(changes: SimpleChanges): void {
    if ('defaultAddOns' in changes && changes.defaultAddOns.currentValue !== undefined) {
      this.selectedAddOns = changes.defaultAddOns.currentValue;
    }
  }

  public closeHandler() {
    this.close.emit();
  }

  public addOnChangeHandler(addOns: IAddOn[], addOnGroupIndex: number): void {
    const groupId = this.getAddOnGroupId(addOnGroupIndex);
    this.removeAddOnsExistsBefore(groupId);
    this.addNewAddOns(addOns);
    this.changed.emit(this.selectedAddOns);
  }

  private getAddOnGroupId(index: number): number {
    return this.item.addOnList[index].id
  }

  private removeAddOnsExistsBefore(groupId: number): void {
    this.selectedAddOns = this.selectedAddOns.filter(item => item.addOnGroupId !== groupId);
  }

  private addNewAddOns(newAddOns: IAddOn[]): void {
    newAddOns.forEach(item => this.selectedAddOns.push(item));
  }
}
