import { Location } from '@angular/common';
import { Component, OnDestroy, OnInit } from '@angular/core';
import { Subscription } from 'rxjs';
import { IProfileOrder, IProfileOrderDetials } from 'src/app/business/abstract/cash-register.types';
import { NullProfileOrder } from 'src/app/business/abstract/null-objects';
import { ProfileService } from 'src/app/business/profile.service';

@Component({
  selector: 'creg-receipt-layout',
  templateUrl: './receipt-layout.component.html',
  styleUrls: ['./receipt-layout.component.scss']
})
export class ReceiptLayoutComponent implements OnInit, OnDestroy {

  public orderDetails: IProfileOrderDetials = {} as IProfileOrderDetials;
  public detailsSubscription: Subscription;

  constructor(
    private profileService: ProfileService,
    private location: Location
  ) {
    this.detailsSubscription = this.profileService.orderDetails.subscribe(res => {
      if ('id' in res) {
        this.orderDetails = res;
      }
      else {
        this.profileService.loadOrderDetails();
      }
    })
  }

  ngOnInit(): void {
  }

  ngOnDestroy(): void {
    this.detailsSubscription?.unsubscribe();
  }

  detailsLoaded(): boolean {
    return 'id' in this.orderDetails;
  }

}
