import { IOrder, IOrderItem } from '../abstract/cash-register.types';
import { IRegisteredOrder, IRegisteredOrderItem, IServerOrder, IServerOrderItem } from '../abstract/api.types';

export function orderConvertor(order: IOrder, mobileNumber: string): IServerOrder {
    const result: IServerOrder = {
      Description: order.customerDescription,
      TotalOrderItemsPrice: order.netPrice,
      OrderItems: order.items.map(item => orderItemConvertor(item))
    }
    if (mobileNumber !== '') {
      result.buyerMobileNumber = mobileNumber;
    }
    return result;
}

function orderItemConvertor(orderItem: IOrderItem): IServerOrderItem {
    return {
      ProductId: orderItem.orderableId,
      Count: orderItem.count,
      AddOnIds: orderItem.addOns.map(item => item.id)
    };
}