import { Component, OnInit, HostBinding } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { Observable, Subscription } from 'rxjs';
import { IMerchant, IOrder } from 'src/app/business/abstract/cash-register.types';
import { NullOrder } from 'src/app/business/abstract/null-objects';
import { MerchantService } from 'src/app/business/merchant.service';
import { OrderService } from 'src/app/business/order.service';

@Component({
  selector: 'creg-menu-layout',
  templateUrl: './menu-layout.component.html',
  styleUrls: ['./menu-layout.component.scss']
})
export class MenuLayoutComponent implements OnInit {

  @HostBinding('class.fixed') fixedMenu: boolean = false;

  public merchant$: Observable<IMerchant>;
  public parentCategoryIndex$: Observable<number>;
  public currentCategoryId: number;
  public order: IOrder;
  public orderSubscription: Subscription;
  public totalCount = 0;
  public totalPrice = 0;

  constructor(
    private merchantService: MerchantService,
    private orderService: OrderService,
    private route: ActivatedRoute,
    private router: Router
  ) {
    this.order = NullOrder;
    this.merchant$ = this.merchantService.merchant;
    this.parentCategoryIndex$ = this.merchantService.parentCategoryIndex;
    this.currentCategoryId = 0;
    this.orderSubscription = this.orderService.order.subscribe(newOrder => {
      this.order = newOrder;
      this.totalCount = this.order.totalCount;
      this.totalPrice = this.order.totalPrice;
    });
  }

  ngOnInit(): void {
    this.merchantService.merchant.subscribe(res => {
      this.route.params.subscribe(params => {
        if ('id' in params) {
          this.merchantService.selectParentCategory(+params.id);
        }
      });
    });
  }

  onScroll($event: any) {
    const scrollTop = $event.srcElement.scrollTop;
    this.merchantService.updateMenuScrollTop(scrollTop);
  }

  basketClickHandler(): void {
    this.router.navigate(['order']);
  }
}
