import { Component, EventEmitter, Input, OnChanges, OnInit, Output, SimpleChanges, ViewChild } from '@angular/core';

@Component({
  selector: 'creg-shared-text-field',
  templateUrl: './text-field.component.html',
  styleUrls: ['./text-field.component.scss']
})
export class TextFieldComponent implements OnInit, OnChanges {

  @Input() state: 'default' | 'success' | 'error' | 'info' = 'default';
  @Input() type: 'farsi' | 'number' | 'english' | 'mobile' = 'farsi';
  @Input() defaultValue: string | number = '';
  @Input() message = '';
  @Input() disabled = false;
  @Input() placeholder = '';
  @Output() valueChanged = new EventEmitter();
  @ViewChild('input') input: any;

  public placeholderFixed = true;
  public stateClass: string = '';
  public textAlign: 'right' | 'left' | 'center' = 'left';
  public value = '';
  public disabledInput = false;
  public filled = false;

  constructor() { }

  ngOnInit(): void {
  }

  ngOnChanges(changes: SimpleChanges): void {
    if ('state' in changes && changes.state.currentValue !== undefined) {
      this.stateClass = changes.state.currentValue;
    }
    if ('type' in changes) {
      if (changes.type.currentValue === 'farsi') {
        this.textAlign = 'right';
      }
      else if (changes.type.currentValue === 'english' || changes.type.currentValue === 'number') {
        this.textAlign = 'left';
      }
    }
    if ('defaultValue' in changes) {
      const value = changes.defaultValue.currentValue === 0 
        ? '' : changes.defaultValue.currentValue + '';
      this.value = value;
      if (parseInt(this.value) > 0) {
        this.placeholderFixed = false;
      }
    }
    this.disabledInput = 'disabled' in changes && changes.disabled.currentValue === true;
  }

  public placeholderClickHandler(): void {
    this.placeholderFixed = false;
    setTimeout(() => {
      this.input.nativeElement.focus();
    }, 10);
  }

  /**
   * clearHandler()  */
  public clearHandler() {
    this.value = '';
    this.input.nativeElement.value = '';
    this.input.nativeElement.focus();
    this.valueChanged.emit('');
  }

  public onChange(value: any): void {
    this.filled = value !== '' && value !== null;
    let allowEmit = true;
    if (this.type === 'mobile') {
      if (value.length > 0 && !/^[0-9]$/.test(value.substr(-1))) {
        allowEmit = false;
        this.input.nativeElement.value = value.substr(0, value.length - 1);
      }
      else if (value.length > 11) {
        allowEmit = false;
        this.input.nativeElement.value = value.substr(0, 11);
      }
    }
    if (allowEmit) {
      this.valueChanged.emit(value);
    }
  }

  public inputBlurHandler($event: any): void {
    const value = +this.input.nativeElement.value;
    if (value === 0) {
      this.placeholderFixed = true;
    }
  }
}
