import { Component } from '@angular/core';
import { Router } from '@angular/router';
import { AuthService } from 'src/app/business/auth.service';
import { OrderService } from 'src/app/business/order.service';

@Component({
  selector: 'creg-loggedin',
  templateUrl: './loggedin.component.html',
  styleUrls: ['./loggedin.component.scss']
})
export class LoggedinComponent {

  constructor(
    private authService: AuthService,
    private orderService: OrderService,
    private router: Router
  ) {
    const redirect = this.authService.getRedirect();
    if (redirect === 'profile') {
      this.router.navigate(['profile']);
    }
    else if (redirect === 'order') {
      this.orderService.saveOrder().then(result => {
        if (result) {
          this.router.navigate(['payment']);
          this.authService.setRedirect('twobackpage');
        }
        else {
          this.router.navigate(['order']);
        }
      })
    }
    else if (redirect === 'payment') {
      this.router.navigate(['payment']);
      this.authService.setRedirect('twobackpage');
    }
    else if (redirect === 'backpage') {
      window.history.back();
    }
    else if (redirect === 'twobackpage') {
      window.history.back();
      window.history.back();
    }
  }

}
