import { inject, Injectable, NgModule } from '@angular/core';
import { ActivatedRouteSnapshot, CanActivate, Router, RouterModule, RouterStateSnapshot, Routes, UrlTree } from '@angular/router';
import { HomepageLayoutComponent } from './homepage/homepage-layout/homepage-layout.component';
import { MenuLayoutComponent } from './menu/menu-layout/menu-layout.component';
import { OrderLayoutComponent } from './order/order-layout/order-layout.component';
import { PaymentLayoutComponent } from './payment/payment-layout/payment-layout.component';
import { ProfileLayoutComponent } from './profile/profile-layout/profile-layout.component';
import { ReceiptLayoutComponent } from './receipt/receipt-layout/receipt-layout.component';
import { ProfileOrdersLayoutComponent } from './profile-orders/profile-orders-layout/profile-orders-layout.component';
import { AuthService } from './business/auth.service';
import { CloseIframeComponent } from './shared/components/close-iframe/close-iframe.component';
import { LoggedinComponent } from './pages/loggedin/loggedin.component';
import { LoggedoutComponent } from './pages/loggedout/loggedout.component';

const AuthGuard = () => {
  if (inject(AuthService).isLoggedIn()) {
    return true;
  }
  inject(Router).navigate(['/']);
  return false;
};

const routes: Routes = [
  {path: '', component: HomepageLayoutComponent},
  {path: 'menu/:id/:title', component: MenuLayoutComponent},
  {path: 'order', component: OrderLayoutComponent},
  {path: 'close-iframe', component: CloseIframeComponent},
  {path: 'loggedin', component: LoggedinComponent},
  {path: 'loggedout', component: LoggedoutComponent},
  {path: 'payment', component: PaymentLayoutComponent},
  {
    path: 'receipt',
    component: ReceiptLayoutComponent,
    canActivate: [AuthGuard]
  },
  {
    path: 'profile',
    component: ProfileLayoutComponent,
    canActivate: [AuthGuard]
  },
  {
    path: 'profile-orders',
    component: ProfileOrdersLayoutComponent,
    canActivate: [AuthGuard]
  }
];

@NgModule({
  imports: [RouterModule.forRoot(routes, {
    anchorScrolling: 'enabled'
  })],
  exports: [RouterModule]
})
export class AppRoutingModule { }
