import { Component, EventEmitter, OnInit, HostListener, Output } from '@angular/core';

@Component({
  selector: 'creg-shared-modal',
  templateUrl: './modal.component.html',
  styleUrls: ['./modal.component.scss']
})
export class ModalComponent implements OnInit {

  @Output() close = new EventEmitter();

  @HostListener('click', ['$event']) myClick(e: any) {
    e.stopPropagation();
    this.close.emit();
    e.preventDefault();
  }

  constructor() { }

  ngOnInit(): void {
  }

}
