import { Injectable } from '@angular/core';
import { BehaviorSubject, Observable } from 'rxjs';
import { IAddOn, IOrder, IOrderableItem, IOrderItem } from './abstract/cash-register.types';
import { IRegisteredOrder, IServerResponse } from './abstract/api.types';
import { NullOrder, NullOrderItem, NullRegisteredOrder } from './abstract/null-objects';
import { HttpClient } from '@angular/common/http';
import { orderConvertor } from './convertors/order.convertors';
import { MerchantService } from './merchant.service';
import { Router } from '@angular/router';
import { ApiService } from './api.service';

@Injectable({
  providedIn: 'root'
})
export class OrderService {

  private orderData: IOrder;
  private orderSubject: BehaviorSubject<IOrder>;
  private maxOrderItemCount = 1000;
  public order: Observable<IOrder>;
  private registeredOrder: IRegisteredOrder = NullRegisteredOrder;

  constructor(
    private httpClient: HttpClient,
    private merchantService: MerchantService,
    private router: Router,
    private apiService: ApiService
  ) {
    this.orderData = NullOrder;
    this.orderSubject = new BehaviorSubject<IOrder>(this.orderData);
    this.order = this.orderSubject.asObservable();
  }

  public getMaxOrderItemCount(): number {
    return this.maxOrderItemCount;
  }

  public updateItem(addOnsHash: string, count: number): void {
    if (!this.isValidOrderItemCount(count)) return;
    this.orderData.items = this.orderData.items.map(item => {
      item.count = item.addOnsHash === addOnsHash ? count : item.count;
      return item;
    });
    this.orderData.items = this.orderData.items.filter(item => item.count > 0);
    this.updateTotalPropertiesOfOrder();
    this.notifyOrder();
  }

  public isValidOrderItemCount(count: number): boolean {
    return this.maxOrderItemCount >= count && count >= 0;
  }

  public saveOrderWithMobile(mobileNumber: string): Promise<boolean> {
    return new Promise((resolve, reject) => {
      let order = orderConvertor(this.orderData, mobileNumber);
      this.httpClient.post<IServerResponse<IRegisteredOrder>>(this.apiService.getApi() + 'Order', order).subscribe(res => {
        this.registeredOrder = res.value;
        resolve(true);
      }, err => {
        resolve(false);
      });
    });
  }

  public saveOrder(): Promise<boolean> {
    return new Promise((resolve, reject) => {
      const merchantId = this.merchantService.getMerchantId();
      const buyerId = 2;
      const tableNumber = '3';
      let order = orderConvertor(this.orderData, '');
      if (this.registeredOrder.orderId > 0) {
        order.orderId = this.registeredOrder.orderId;
        this.httpClient.put<IServerResponse<IRegisteredOrder>>(this.apiService.getApi() + 'Order', order).subscribe(res => {
          this.registeredOrder = res.value;
          resolve(true);
        }, err => {
          resolve(false);
        });
      }
      else {
        this.httpClient.post<IServerResponse<IRegisteredOrder>>(this.apiService.getApi() + 'Order', order).subscribe(res => {
          this.registeredOrder = res.value;
          resolve(true);
        }, err => {
          resolve(false);
        });
      }
    })
  }

  public getRegisteredOrder(): IRegisteredOrder {
    return this.registeredOrder;
  }

  public editItem(orderableItem: IOrderableItem, addOns: IAddOn[], addOnHash: string): void {
    let currentCount = 0;
    this.orderData.items.forEach(item => {
      if (item.addOnsHash === addOnHash) {
        currentCount = item.count;
      }
    });
    if (this.isValidOrderItemCount(currentCount)) {
      this.updateItem(addOnHash, 0);
      this.addItem(orderableItem, addOns, currentCount);
    }
  }

  public addItem(orderableItem: IOrderableItem, addOns: IAddOn[], count: number): IOrderItem {
    let result = NullOrderItem;
    this.orderData.items.forEach((item, index) => {
      if (item.addOnsHash === this.getOrderHash(orderableItem, addOns)) {
        const newCount = this.orderData.items[index].count + count;
        if (this.isValidOrderItemCount(newCount)) {
          this.orderData.items[index].count = newCount;
        }
        result = this.orderData.items[index];
      }
    });
    if (result.isNull() && this.isValidOrderItemCount(count)) {
      result = this.createNewOrderItem(orderableItem, addOns, count);
      this.orderData.items.push(result);
    }
    this.updateTotalPropertiesOfOrder();
    this.notifyOrder();
    return result;
  }

  public deleteAll(): void {
    this.orderData.items = [];
    this.updateTotalPropertiesOfOrder();
    this.notifyOrder();
  }

  private updateTotalPropertiesOfOrder(): void {
    this.orderData.netPrice = this.orderData.items
      .map(item => item.totalPrice * item.count)
      .reduce((p, c) => p + c, 0);

    this.orderData.totalCount = this.orderData.items
      .map(item => item.count)
      .reduce((p, c) => p + c, 0);

    this.orderData.totalDiscount = 0;
    this.orderData.totalPrice = this.orderData.netPrice;
  }

  public updateDescription(description: string): void {
    this.orderData.customerDescription = description;
    this.notifyOrder();
  }

  private notifyOrder(): void {
    this.orderSubject.next(this.orderData);
  }

  private createNewOrderItem(orderableItem: IOrderableItem, addOns: IAddOn[], count: number): IOrderItem {
    return {
      id: 0,
      title: orderableItem.title,
      imageSrc: orderableItem.imageSrc,
      addOnsDescription: addOns.map(item => item.name).join('، '),
      orderableId: orderableItem.id,
      price: orderableItem.price,
      addOns: addOns,
      count: count,
      totalPrice: orderableItem.price + addOns.map(item => item.price).reduce((p, c) => p + c, 0),
      addOnsHash: this.getOrderHash(orderableItem, addOns),
      isNull: () => false
    };
  }

  private getOrderHash(orderableItem: IOrderableItem, addOns: IAddOn[]): string {
    const ids = [orderableItem.id];
    addOns.forEach(item => ids.push(item.id));
    return ids.sort().join('_');
  }
}
