import { Location } from '@angular/common';
import { Component, OnInit } from '@angular/core';
import { IRegisteredOrder } from 'src/app/business/abstract/api.types';
import { IOrder } from 'src/app/business/abstract/cash-register.types';
import { NullOrder, NullRegisteredOrder } from 'src/app/business/abstract/null-objects';
import { OrderService } from 'src/app/business/order.service';

@Component({
  selector: 'creg-payment-layout',
  templateUrl: './payment-layout.component.html',
  styleUrls: ['./payment-layout.component.scss']
})
export class PaymentLayoutComponent implements OnInit {

  public registeredOrder: IRegisteredOrder = NullRegisteredOrder;
  public order: IOrder = NullOrder;
  public reward: number = 0;
  public walletAmount: number = 0;
  public tableNumber: number = 0;
  public reservedTable: number = 0;

  constructor(
    private orderService: OrderService,
    private location: Location
  ) {
    this.registeredOrder = this.orderService.getRegisteredOrder();
    this.reservedTable = this.registeredOrder.tableNumber ? +this.registeredOrder.tableNumber : 0;
    this.orderService.order.subscribe(res => {
      this.order = res;
    });
  }

  ngOnInit(): void {
  }

  public setReward(newReward: number): void {
    this.reward = newReward;
  }

  public setWalletAmount(walletAmount: number): void {
    this.walletAmount = walletAmount;
  }

  public setTableNumber(newTableNumber: number): void {
    this.tableNumber = newTableNumber;
  }

  public backHandler(): void {
    this.location.back();
  }
}
