import { Component, Input, OnChanges, OnInit, SimpleChanges } from '@angular/core';
import { IRegisteredOrder } from 'src/app/business/abstract/api.types';
import { NullRegisteredOrder } from 'src/app/business/abstract/null-objects';

@Component({
  selector: 'creg-payment-accounting',
  templateUrl: './accounting.component.html',
  styleUrls: ['./accounting.component.scss']
})
export class AccountingComponent implements OnInit, OnChanges {

  @Input() order: IRegisteredOrder = NullRegisteredOrder;
  @Input() wallet: number = 0;
  @Input() reward: number = 0;
  @Input() totalCount: number = 0;

  public totalPrice: number = 0;
  public payable: number = 0;

  constructor() { }

  ngOnInit(): void {
  }

  ngOnChanges(changes: SimpleChanges): void {
    if ('wallet' in changes || 'order' in changes || 'reward' in changes) {
      this.calculateTotalPrice();
    }
  }

  private calculateTotalPrice(): void {
    let result = 0;
    if (this.reward !== undefined) {
      result += this.reward;
    }
    if (this.order !== undefined) {
      result += this.order.paymentPrice + this.order.tax + this.order.serviceCharge - this.order.discount;
    }
    this.totalPrice = result;
    const payable = this.totalPrice - this.order.prePayment;
    this.payable = this.wallet > payable ? 0 : payable - this.wallet;
  }

}
