import { Location } from '@angular/common';
import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { AuthService } from 'src/app/business/auth.service';
import { ProfileService } from 'src/app/business/profile.service';

@Component({
  selector: 'creg-profile-layout',
  templateUrl: './profile-layout.component.html',
  styleUrls: ['./profile-layout.component.scss']
})
export class ProfileLayoutComponent implements OnInit {

  public profile = this.profileService.profile;

  constructor(
    private profileService: ProfileService,
    private location: Location,
    private router: Router,
    private authService: AuthService
  ) {
    this.profileService.loadProfile();
  }

  ngOnInit(): void {
  }

  backHandler(): void {
    this.authService.setRedirect('backpage');
    this.location.back();
  }

  logoutHandler() {
    this.authService.logout();
    this.router.navigate(['/']);
  }

  /**
   * ordersHandler()
   */
  public ordersHandler() {
    this.router.navigate(['profile-orders']);
  }
}
