import { Component, EventEmitter, OnInit, Output } from '@angular/core';

@Component({
  selector: 'creg-order-delete-all-modal',
  templateUrl: './delete-all-modal.component.html',
  styleUrls: ['./delete-all-modal.component.scss']
})
export class DeleteAllModalComponent implements OnInit {

  @Output() approve = new EventEmitter<boolean>();
  @Output() dismiss = new EventEmitter<boolean>();

  constructor() { }

  ngOnInit(): void {}

  /**
   * approveHandler
   */
  public approveHandler(e: any) {
    this.approve.emit();
  }

  /**
   * dismissHandler
   */
  public dismissHandler(e: any) {
    console.log('dismiss')
    this.dismiss.emit(e);
  }
}
