import { Component } from '@angular/core';
import { AuthService } from 'src/app/business/auth.service';

@Component({
  selector: 'creg-login-iframe',
  templateUrl: './login-iframe.component.html',
  styleUrls: ['./login-iframe.component.scss']
})
export class LoginIframeComponent {

  constructor(
    private authService: AuthService
  ) {}

  public closeHandler(): void {
    this.authService.closeIFrame();
  }

  public onIframeClosed($event: any): void {
    console.log('iframe closed', {$event});
  }
}
