import { Component, OnInit, Input, AfterViewInit } from '@angular/core';
import { ICategory } from 'src/app/business/abstract/cash-register.types';
import { MerchantService } from 'src/app/business/merchant.service';
import { Observable } from 'rxjs';

@Component({
  selector: 'creg-menu-category-bar',
  templateUrl: './category-bar.component.html',
  styleUrls: ['./category-bar.component.scss']
})
export class MenuCategoryBarComponent implements OnInit, AfterViewInit {

  @Input() categories: ICategory[];
  public categoryId: Observable<number>;
  public menuScrollTop: Observable<number>;

  constructor(
    private merchantService: MerchantService
  ) {
    this.categoryId = this.merchantService.categoryId;
    this.menuScrollTop = this.merchantService.menuScrollTop;
    this.categories = [];
  }

  ngOnInit(): void {}

  ngAfterViewInit(): void {
    let observeable = this.categoryId.pipe().subscribe(res => {
      if (+res > 0) {
        this.scrollToCategory(+res)
        setTimeout(() => {
          observeable.unsubscribe();
        }, 0);
      }
    });
  }

  public selectCategory(categoryId: number, categoryIndex: number): void {
    this.scrollToCategory(categoryId);
  }

  private scrollToCategory(categoryId: number): void {
    const categoryElement = document.getElementById('category' + categoryId);
    const offsetTop = categoryElement === null ? 0 : categoryElement.offsetTop;
    const options: ScrollToOptions = {
      top: offsetTop - 120,
      behavior: 'smooth'
    }
    document.getElementById('menu-layout')?.scrollTo(options);
  }
}
